.programs {
  padding-top: 90px;
  padding-bottom: 100px;

  // &__inner

  &__top {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px; }

  // &__title

  // &__tariffs

  &__head {
    width: 260px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-bottom: 20px; }

  &__bottom {
    display: flex;
    justify-content: flex-end;
    margin-top: 20px; } }

.tariffs {
  // width: 65%
  width: calc((100% - 30px) / 100 * 65);
  flex-shrink: 0;
  display: flex;

  &__wrap {
    width: calc(100% / 3);
    display: block;
    padding: 40px 30px;
    border-radius: 10px;

    &:hover {
      background: linear-gradient(360deg, #7551DA 0%, #A584FF 100%);

      .tariffs {
        &__title {
          color: $white; }

        &__prise {
          color: $white; }

        &__text {
          color: #D9D1E0; } } } }

  &__title {
    @include font-size(1.25rem);
    @include margin-bottom(2.5rem);
    font-weight: 700;
    color: $text;
    text-align: center; }

  &__prise {
    font-weight: 700;
    margin-bottom: 10px;
    color: $text;
    text-align: center; }

  &__text {
    font-size: 14px;
    color: #C5C0CA;
    letter-spacing: -0.233333px;
    text-align: center;
    @include margin-bottom(3.125rem);

    @include mw(600) {
      font-size: 16px; } }

  &__link {
    display: block;
    text-align: center;
    font-size: 14px;
    color: #7B57DF;
    padding-top: 17px;
    padding-bottom: 18px;
    background: #F7F8FB;
    border-radius: 4px;
    font-weight: 700;

    @include mw(600) {
      font-size: 16px; } } }

//   &__list
//// &__item
//  // display: flex

.disciplines {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 28px;
  padding-bottom: 28px;
  padding-left: 30px;
  border-radius: 26px;

  &:nth-child(odd) {
    background: #F7F8FB; }

  &__text {
    font-weight: 500;
    font-size: 16px;
    max-width: 290px; }

  &__inner {
    display: flex;
    width: 65%;
    flex-shrink: 0; }

  &__option {
    width: calc(100% / 3);
    display: flex;
    justify-content: center; }

  &__option--active {} }
