.promo {
  background: #F8F9FC;

  &__inner {
    display: flex;
    padding-top: 30px;

    @include mw(600) {
      flex-direction: column; } }

  &__content {
    width: 50%;
    flex-shrink: 0;
    @include padding-top(12rem);
    @include padding-bottom(10rem);

    @include mw(600) {
      width: 100%; } }

  &__title {
    margin-bottom: 40px; }

  &__text {
    margin-bottom: 84px; }

  &__link {
    max-width: 332px;

    @include mw(600) {
      max-width: none; } }

  &__img-wrap {
    min-width: 50%;
    display: flex;
    align-items: flex-end;

    & picture {
      display: flex; } }

  &__img {} }

.title {
  @include font(Manrope);
  font-weight: 700; }

.btn {
  display: block;
  background: linear-gradient(360deg, #7551DA 0%, #A584FF 100%);
  border-radius: 4px;
  @include font-size(1.125rem);
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  color: $white;
  padding-top: 25px;
  padding-bottom: 25px;
  text-align: center;
  cursor: pointer;
  font-weight: 500;

  &:hover {
    background: linear-gradient(360deg,#8e70e2 0,#a584ff 100%); } }
