.comfortable {
  margin-bottom: 100px;

  &__inner {
    max-width: 1440px;
    padding-top: 45%;
    height: 0;
    position: relative;

    @include mw(1200) {
      padding-top: 0;
      height: auto; } }

  &__content {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    @include padding-top(4.7rem);

    @include mw(1200) {
      position: static;
      @include padding-bottom(20rem); } }

  &__wrap-img {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    overflow: hidden;
    display: flex;
    align-items: flex-start;
    justify-content: center;
    z-index: -1;

    @include mw(1200) {
      align-items: flex-end; } }

  &__img {
    display: block; }

  &__title {
    @include margin-bottom(5.3rem);
    text-align: center; }

  &__list {
    display: flex;
    justify-content: space-around;

    @include mw(768) {
      flex-direction: column; } }

  &__item {
    width: calc((100% / 3) - 100px);

    @include mw(768) {
      width: 100%; } }

  &__text {
    text-align: center;
    color: #131213; } }
